export type HomeView = "Padrao" | "Viagem" | "PertoDeMim" | "SelecionarCoordenadas"

/**
 * Representao de linha nibus na API. Ocorrencias no so utilizadas na exibio de itinerrios.
 */
export type tuplaLinha = {
    geojson?: string,
    apenas_cartao: string,
    categoria: string,
    codigo_linha: string;
    cor: string;
    nome_linha: string;
    ocorrencias?: any[];
}

/**
 * Representao de ponto nibus na API.
 */
export type tuplaPonto = tuplaLinha & {
    tipo: string;
    numero_ponto: number;
    endereco: string;
}


/**
 * Representao na API de um horrio de parada de uma linha num ponto de nibus
 */
export type tuplaHorario = {
    adaptado: string;
    codigo_linha: string;
    numero_ponto: string;
    horario_saida: string;
    ponto: string;
    tipo_dia: string;
}

/**
 * Representao na API de uma rota simples.
 */
export type tuplaRota = {
    codigo_linha: string;
    dados_linha: tuplaLinha;
    dados_ponto_destino: tuplaPonto;
    dados_ponto_origem: tuplaPonto;
    direcao: string;
    info_rota: infoRota[];
}

export type infoRota = {
    geojson: string;
    numero_ponto: number;
    seq: number;
    codigo_linha: string;
}

export type tuplaCaminho = {
    accessibilityCoeficient: number;
    accumulatedDistance: number;
    distance: number;
    effort: number;
    latitude: number;
    longitude: number;
    routeId: number;
    sequence: number;
}

export type tuplaCrowdsourcingLinha = {
    nivel: number;
    tipo: number;
    codigo_linha: string;
    dia: string;
    horario: string;
    lat: number;
    lon: number;
}

export const API_URL = "https://myurb-myurb.a3c1.starter-us-west-1.openshiftapps.com";
